<template>
  <nav id="navBar">
    <router-link
      :to="{ name: 'ProductsCategory', params: { category: 'bananas' }}"
      id="btnBabanas"
      class="button"
    >Cambures</router-link>

    <router-link
      :to="{ name: 'ProductsCategory', params: { category: 'coconuts' }}"
      id="btnCoconuts"
      class="button"
    >Cocos</router-link>

    <router-link
      :to="{ name: 'ProductsCategory', params: { category: 'apples' }}"
      id="btnApples"
      class="button"
    >Manzanas</router-link>

    <router-link
      :to="{ name: 'ProductsCategory', params: { category: 'pears' }}"
      id="btnPears"
      class="button"
    >Peras</router-link>

    <router-link
      :to="{ name: 'ProductsCategory', params: { category: 'pineaples' }}"
      id="btnPineaples"
      class="button"
    >Piñas</router-link>

    <router-link
      :to="{ name: 'ProductsCategory', params: { category: 'grapes' }}"
      id="btnGrapes"
      class="button"
    >Uvas</router-link>
  </nav>
</template>

<script>
export default {
  name: "NavbarProducts",
  data() {
    return {
      showCategory: false,
      clickMenu: false
    };
  },
  mounted() {
    this.showMenuFun();
  },
  methods: {
    showMenuFun() {
      if (this.clickMenu == true) {
        this.clickMenu = false;
      }
      this.showCategory = false;
    }
  },
  watch: {
    clickMenu: "showMenuFun",
    $route() {
      this.showCategory = false;
    }
  }
};
</script>

